.img {
    width: 50px;
    height: 50px;
}

.linhape {
    display: inline-flex;
    align-items: center;
    text-decoration: none;
}

.pe a {
    color: #FFB703;
}

.pe {
    background-color: #023047;
    color: #FFB703;
    text-align: center;
    padding: 20px 0;
    float: left;
    width: 100%;
    margin-top: 50px;
    position: relative;
}

.pe a {
    color: #FFB703;
    position: relative;
    text-decoration: none;
}

.pe a::before {
    content: '';
    position: absolute;
    width: 0;
    height: 2px;
    bottom: -3px;
    left: 0;
    background: #FFB703;
    transition: all 0.2s ease-in-out;
    visibility: visible;
    transform: scaleX(0);
}

.pe a:hover::before {
    width: 100%;
    transform: scaleX(1);
}

