        margin-left: 10px;
        margin-right: 0;
        margin-bottom: 10px;
        position: relative;
        top: 5px;
    }

    .menuToggle {
        display: block;
        order: -1;
    }

    .menu {
        display: none;
        flex-direction: column;
        background-color: #023047;
        width: 100%;
    }

    .menu li {
        width: 100%;
        text-align: center;
    }

    .nav.active .menu {
        display: flex;
    }

    .submenu {
        position: static;
        width: 100%;
    }

